@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

/* ============================================================
   Ï≤≠ÏõêÍ≥† ÏàòÎä•ÎåÄÎ∞ï - ÌÜµÌï© Ïä§ÌÉÄÏùºÏãúÌä∏
   ============================================================ */

* { margin: 0; padding: 0; box-sizing: border-box; }

body {
  font-family: 'Varela Round', 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background 0.3s ease;
  position: relative;
  overflow: hidden;
}

/* === Î∞∞Í≤Ω === */
.App.dark-mode {
  background: radial-gradient(circle at 20% 30%, rgba(255,198,88,0.2) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(139,69,19,0.15) 0%, transparent 50%),
    linear-gradient(135deg, #8B4513 0%, #A0522D 50%, #CD853F 100%);
}
.App.light-mode {
  background: radial-gradient(circle at 20% 30%, rgba(255,255,255,0.5) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255,198,88,0.3) 0%, transparent 50%),
    linear-gradient(135deg, #FFF8DC 0%, #FFEFD5 50%, #FFE4B5 100%);
}

.App::before { content: 'üçØ'; position: absolute; top: 50px; left: 100px; font-size: 80px; opacity: 0.1; animation: honeyDrop 8s ease-in-out infinite; }
.App::after { content: 'üß∏'; position: absolute; bottom: 80px; right: 120px; font-size: 100px; opacity: 0.15; animation: bearFloat 6s ease-in-out infinite; }

@keyframes honeyDrop { 0%, 100% { transform: translateY(0px) rotate(0deg); } 50% { transform: translateY(20px) rotate(10deg); } }
@keyframes bearFloat { 0%, 100% { transform: translateY(0px) rotate(-5deg); } 50% { transform: translateY(-15px) rotate(5deg); } }

.App.timer-active.dark-mode { background: linear-gradient(135deg, #A0522D 0%, #CD853F 100%); }
.App.timer-active.light-mode { background: linear-gradient(135deg, #FFF8DC 0%, #FFEFD5 100%); }

.container { max-width: 800px; width: 100%; position: relative; z-index: 1; }

/* === Ìó§Îçî === */
.header { text-align: center; margin-bottom: 30px; position: relative; }
.light-mode .header { color: #8B4513; }
.dark-mode .header { color: #FFC658; }
.header h1 { font-size: 3em; margin-bottom: 10px; font-weight: 700; text-shadow: 3px 3px 0px rgba(139,69,19,0.2); }
.subtitle { font-size: 1.2em; opacity: 0.9; font-weight: 600; }

.header-buttons { display: flex; gap: 12px; justify-content: center; margin-top: 25px; }

.icon-btn {
  background: #FFC658; border: 4px solid #8B4513; border-radius: 20px;
  width: 55px; height: 55px; font-size: 1.6em; cursor: pointer;
  transition: all 0.3s ease; box-shadow: 0 6px 0px #8B4513; position: relative;
}
.icon-btn:hover { transform: translateY(-3px) rotate(-3deg); box-shadow: 0 9px 0px #8B4513; }
.icon-btn:active { transform: translateY(3px); box-shadow: 0 3px 0px #8B4513; }

/* === Î∞∞ÎÑà === */
.dday-banner {
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  border: 4px solid #8B4513; border-radius: 25px; padding: 18px 25px;
  text-align: center; color: #8B4513; font-size: 1.3em; margin-bottom: 18px;
  box-shadow: 0 6px 0px #8B4513; font-weight: 700; position: relative;
}
.dday-banner::before { content: 'üêª'; position: absolute; left: 20px; top: 50%; transform: translateY(-50%); font-size: 1.8em; }
.dday-banner::after { content: 'üçØ'; position: absolute; right: 20px; top: 50%; transform: translateY(-50%); font-size: 1.5em; }

.streak-banner {
  background: linear-gradient(135deg, #CD853F 0%, #DEB887 100%);
  border: 4px solid #8B4513; border-radius: 25px; padding: 18px 25px;
  text-align: center; color: white; font-size: 1.3em; margin-bottom: 18px;
  box-shadow: 0 6px 0px #8B4513; font-weight: 700; position: relative;
  text-shadow: 2px 2px 0px rgba(0,0,0,0.2);
}
.streak-banner::before { content: 'üß∏'; position: absolute; left: 20px; top: 50%; transform: translateY(-50%); font-size: 1.8em; }

/* === ÌÄµ Ïù∏Ìè¨ === */
.quick-info { display: flex; gap: 12px; margin-bottom: 22px; }
.quick-info-item {
  flex: 1; background: #FFF8DC; border: 4px solid #CD853F; border-radius: 20px;
  padding: 16px; text-align: center; color: #8B4513; font-size: 0.95em;
  box-shadow: 0 5px 0px #CD853F; font-weight: 700; position: relative;
}
.dark-mode .quick-info-item { background: rgba(255,198,88,0.25); border-color: #FFC658; box-shadow: 0 5px 0px #FFC658; color: #FFC658; }
.quick-info-item span { display: block; opacity: 0.85; font-size: 0.9em; margin-bottom: 5px; }
.quick-info-item strong { display: block; font-size: 1.3em; }

/* === Î™®ÏùòÍ≥†ÏÇ¨ Î≤ÑÌäº === */
.mock-exam-btn {
  width: 100%; padding: 22px;
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  color: #8B4513; border: 5px solid #8B4513; border-radius: 30px;
  font-size: 1.4em; font-weight: 700; cursor: pointer; margin-bottom: 28px;
  transition: all 0.3s ease; box-shadow: 0 8px 0px #8B4513; position: relative;
}
.mock-exam-btn:hover { transform: translateY(-4px); box-shadow: 0 12px 0px #8B4513; }
.mock-exam-btn:active { transform: translateY(4px); box-shadow: 0 4px 0px #8B4513; }

/* === Í≥ºÎ™© Ïπ¥Îìú Í∑∏Î¶¨Îìú === */
.subject-grid { display: grid; grid-template-columns: repeat(auto-fit, minmax(145px, 1fr)); gap: 18px; margin-bottom: 28px; }
.subject-card {
  background: #FFF8DC; border: 4px solid; border-radius: 25px; padding: 28px 18px;
  text-align: center; cursor: pointer; transition: all 0.3s ease; box-shadow: 0 6px 0px currentColor; position: relative;
}
.dark-mode .subject-card { background: rgba(255,198,88,0.2); }
.subject-card:hover { transform: translateY(-8px) rotate(-2deg); box-shadow: 0 10px 0px currentColor; }
.subject-card:active { transform: translateY(3px); box-shadow: 0 3px 0px currentColor; }
.subject-emoji { font-size: 3.5em; margin-bottom: 12px; }
.subject-name { font-size: 1.3em; font-weight: 700; color: #8B4513; margin-bottom: 8px; }
.dark-mode .subject-name { color: #FFC658; }
.subject-time { font-size: 1.1em; font-weight: 700; color: #CD853F; }

/* === Ï†ïÎ≥¥ Î∞ïÏä§ === */
.info-box {
  background: #FFF8DC; border: 4px solid #CD853F; border-radius: 25px;
  padding: 22px 28px; color: #8B4513; text-align: center;
  box-shadow: 0 6px 0px #CD853F; position: relative;
}
.dark-mode .info-box { background: rgba(255,198,88,0.2); border-color: #FFC658; box-shadow: 0 6px 0px #FFC658; color: #FFC658; }
.info-box::before { content: 'üß∏'; position: absolute; top: -20px; left: 50%; transform: translateX(-50%); font-size: 2.5em; }
.info-box p { margin: 9px 0; font-size: 1em; font-weight: 600; line-height: 1.6; }

/* === Î™®Îã¨ === */
.modal-overlay {
  position: fixed; top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(139,69,19,0.75); display: flex; align-items: center;
  justify-content: center; z-index: 1000; padding: 20px; backdrop-filter: blur(10px);
}
.modal-content {
  background: #FFF8DC; border: 6px solid #8B4513; border-radius: 35px;
  padding: 40px; max-width: 500px; width: 100%; box-shadow: 0 10px 0px #8B4513;
  animation: bearPop 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55); position: relative;
}
@keyframes bearPop { 0% { transform: scale(0); opacity: 0; } 100% { transform: scale(1); opacity: 1; } }
.modal-content::before { content: 'üêª'; position: absolute; top: -30px; left: 50%; transform: translateX(-50%); font-size: 3.5em; }
.modal-content h2 { text-align: center; margin-bottom: 20px; color: #8B4513; font-size: 2.2em; font-weight: 700; }
.modal-content p { text-align: center; color: #8B4513; margin-bottom: 28px; font-size: 1.1em; font-weight: 600; }
.modal-desc { font-size: 0.95em !important; opacity: 0.8; }

/* === Ìú¥Ïãù/Î™®ÏùòÍ≥†ÏÇ¨ Î™®Îã¨ Î≤ÑÌäº === */
.break-options { display: flex; gap: 10px; margin-bottom: 18px; }
.break-options button {
  flex: 1; padding: 18px;
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  color: #8B4513; border: 4px solid #8B4513; border-radius: 18px;
  font-size: 1em; font-weight: 700; cursor: pointer; box-shadow: 0 5px 0px #8B4513; transition: all 0.3s;
}
.break-options button:hover { transform: translateY(-2px); box-shadow: 0 7px 0px #8B4513; }
.break-options button:active { transform: translateY(2px); box-shadow: 0 3px 0px #8B4513; }

.skip-btn {
  width: 100%; padding: 16px; background: #CD853F; color: white;
  border: 4px solid #8B4513; border-radius: 18px; font-size: 1em; font-weight: 700;
  cursor: pointer; transition: all 0.3s; text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
}
.skip-btn:hover { background: #DEB887; transform: translateY(-2px); }
.skip-btn:disabled { opacity: 0.4; cursor: not-allowed; }

.mock-exam-schedule { background: linear-gradient(135deg, #FFEFD5 0%, #FFE4B5 100%); border: 4px solid #CD853F; border-radius: 22px; padding: 20px; margin-bottom: 20px; }
.schedule-item { display: flex; align-items: center; justify-content: center; padding: 12px 14px; background: white; border: 3px solid #FFC658; border-radius: 18px; margin-bottom: 10px; text-align: center; }
.schedule-item:last-child { margin-bottom: 0; }
.schedule-subject { font-size: 1.1em; font-weight: 700; color: #8B4513; line-height: 1.5; }
.total-time { text-align: center; color: #8B4513; font-weight: 700; margin-bottom: 28px; font-size: 1.1em; }

.modal-buttons { display: flex; gap: 10px; }
.start-mock-btn {
  flex: 1; padding: 18px; background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
  color: #8B4513; border: 4px solid #8B4513; border-radius: 18px;
  font-size: 1.2em; font-weight: 700; cursor: pointer; box-shadow: 0 5px 0px #8B4513; transition: all 0.3s;
}
.start-mock-btn:hover { transform: translateY(-2px); box-shadow: 0 7px 0px #8B4513; }
.cancel-btn {
  flex: 1; padding: 18px; background: #CD853F; color: white;
  border: 4px solid #8B4513; border-radius: 18px; font-size: 1.2em; font-weight: 700;
  cursor: pointer; transition: all 0.3s; text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
}
.cancel-btn:hover { background: #DEB887; transform: translateY(-2px); }

/* === Ïπ¥Ïö¥Ìä∏Îã§Ïö¥ ÌôîÎ©¥ === */
.ready-container { max-width: 600px; width: 100%; text-align: center; color: #8B4513; }
.dark-mode .ready-container { color: #FFC658; }
.ready-subject { font-size: 3.5em; font-weight: 700; margin-bottom: 32px; animation: bearSwing 2s ease-in-out infinite; }
@keyframes bearSwing { 0%, 100% { transform: rotate(-3deg); } 50% { transform: rotate(3deg); } }
.ready-tip {
  background: #FFF8DC; border: 5px solid #CD853F; border-radius: 28px;
  padding: 32px; font-size: 1.3em; line-height: 1.7; margin-bottom: 40px;
  box-shadow: 0 8px 0px #CD853F; font-weight: 600; color: #8B4513; position: relative;
}
.dark-mode .ready-tip { background: rgba(255,198,88,0.25); border-color: #FFC658; box-shadow: 0 8px 0px #FFC658; color: #FFC658; }
.ready-tip::before { content: 'üçØ'; position: absolute; top: -20px; left: 50%; transform: translateX(-50%); font-size: 2.5em; }
.ready-countdown { font-size: 9em; font-weight: 700; margin-bottom: 22px; animation: bearHop 1s infinite; }
@keyframes bearHop { 0%, 100% { transform: scale(1); } 50% { transform: scale(1.15); } }
.ready-message { font-size: 1.6em; font-weight: 700; }

/* === ÌÉÄÏù¥Î®∏ ÌôîÎ©¥ === */
.timer-container { max-width: 600px; width: 100%; position: relative; }
.mock-exam-progress {
  text-align: center; color: #8B4513; font-size: 1.2em; margin-bottom: 22px; padding: 15px;
  background: #FFF8DC; border: 4px solid #CD853F; border-radius: 22px; font-weight: 700; box-shadow: 0 5px 0px #CD853F;
}
.dark-mode .mock-exam-progress { background: rgba(255,198,88,0.25); border-color: #FFC658; box-shadow: 0 5px 0px #FFC658; color: #FFC658; }
.timer-header { text-align: center; margin-bottom: 40px; }
.timer-header h2 { color: #8B4513; font-size: 1.6em; font-weight: 700; }
.dark-mode .timer-header h2 { color: #FFC658; }

.timer-display {
  background: #FFF8DC; border: 8px solid #CD853F; border-radius: 35px;
  padding: 40px 30px; text-align: center; margin-bottom: 12px;
  box-shadow: 0 10px 0px #CD853F; transition: all 0.3s ease; position: relative;
}
.dark-mode .timer-display { background: rgba(255,198,88,0.3); border-color: #FFC658; box-shadow: 0 10px 0px #FFC658; }
.timer-display::before { content: 'üêª'; position: absolute; top: -20px; left: 50%; transform: translateX(-50%); font-size: 2em; }
.timer-display.break-mode { background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%); border-color: #8B4513; }
.timer-display.break-mode::before { content: 'üçØ'; }
.timer-display.warning { background: linear-gradient(135deg, #FFF9E6 0%, #FFE9B3 100%); animation: bearWarning 1s infinite; }
.timer-display.critical { background: linear-gradient(135deg, #FFE6E6 0%, #FFB3B3 100%); animation: bearCritical 0.5s infinite; }
@keyframes bearWarning { 0%, 100% { transform: scale(1); box-shadow: 0 10px 0px #FFD700; } 50% { transform: scale(1.02); box-shadow: 0 12px 0px #FFD700; } }
@keyframes bearCritical { 0%, 100% { transform: scale(1) rotate(-1deg); box-shadow: 0 10px 0px #FF6B6B; } 50% { transform: scale(1.04) rotate(1deg); box-shadow: 0 12px 0px #FF6B6B; } }

.time { font-size: 7em; font-weight: 700; color: #8B4513; font-variant-numeric: tabular-nums; letter-spacing: 0.05em; }
.dark-mode .time { color: #FFC658; }
.timer-display.break-mode .time { color: #8B4513; }
.alert-text { margin-top: 22px; font-size: 1.3em; font-weight: 700; color: #FFA500; }
.break-text { margin-top: 22px; font-size: 1.6em; font-weight: 700; color: #8B4513; }
.critical-alert { color: #FF6B6B; animation: bearBlink 0.5s infinite; }
@keyframes bearBlink { 0%, 100% { opacity: 1; } 50% { opacity: 0.5; } }

.focus-score-live { margin-top: 20px; font-size: 1.4em; font-weight: 700; color: #8B4513; animation: scorePulse 2s ease-in-out infinite; }
.dark-mode .focus-score-live { color: #FFC658; }
.timer-display.break-mode .focus-score-live { display: none; }
@keyframes scorePulse { 0%, 100% { opacity: 1; transform: scale(1); } 50% { opacity: 0.85; transform: scale(1.02); } }

.progress-bar { height: 20px; background: #FFE4B5; border: 3px solid #CD853F; border-radius: 12px; overflow: hidden; margin-bottom: 10px; box-shadow: inset 0 3px 6px rgba(139,69,19,0.2); }
.dark-mode .progress-bar { background: rgba(205,133,63,0.3); border-color: #FFC658; }
.progress-fill { height: 100%; transition: width 0.3s ease; border-radius: 18px; background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%); box-shadow: inset 0 -2px 6px rgba(255,255,255,0.5); }

.timer-controls { display: flex; gap: 18px; justify-content: center; position: relative; z-index: 1000; }
.btn { padding: 18px 45px; font-size: 1.3em; font-weight: 700; border: 5px solid #8B4513; border-radius: 22px; cursor: pointer; transition: all 0.3s ease; box-shadow: 0 6px 0px #8B4513; }
.btn:hover { transform: translateY(-3px); box-shadow: 0 9px 0px #8B4513; }
.btn:active { transform: translateY(3px); box-shadow: 0 3px 0px #8B4513; }
.btn-pause { background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%); color: #8B4513; }
.btn-stop { background: #CD853F; color: white; text-shadow: 1px 1px 2px rgba(0,0,0,0.2); }

/* === ÏùºÏãúÏ†ïÏßÄ & ÏôÑÎ£å Ïò§Î≤ÑÎ†àÏù¥ === */
.paused-overlay, .completion-overlay {
  position: fixed; top: 0; left: 0; width: 100vw; height: 100vh;
  z-index: 500; display: flex; flex-direction: column; justify-content: center; align-items: center;
}
.paused-overlay { background-color: rgba(0,0,0,0.8); }
.completion-overlay { background: linear-gradient(135deg, rgba(139,69,19,0.95) 0%, rgba(205,133,63,0.95) 100%); animation: bearFadeIn 0.3s ease; }
@keyframes bearFadeIn { from { opacity: 0; } to { opacity: 1; } }
.pause-main { font-size: 4rem; font-weight: 900; color: #ffffff; margin-bottom: 20px; text-shadow: 0 4px 10px rgba(0,0,0,0.5); }
.pause-tip { font-size: 1.5rem; color: #FFC658; background: rgba(255,255,255,0.1); padding: 15px 30px; border-radius: 50px; max-width: 80%; text-align: center; }
.completion-message { color: #FFC658; font-size: 2.5em; font-weight: 700; text-align: center; animation: bearCelebrate 1s ease infinite; padding: 0 40px; line-height: 1.6; white-space: pre-line; }
@keyframes bearCelebrate { 0%, 100% { transform: scale(1) rotate(-2deg); } 25% { transform: scale(1.1) rotate(2deg); } 50% { transform: scale(1) rotate(-2deg); } 75% { transform: scale(1.1) rotate(2deg); } }

/* === Ï≤¥ÌÅ¨Î¶¨Ïä§Ìä∏ === */
.checklist-header, .notes-header, .stats-header, .settings-header { margin-bottom: 12px; }
.checklist-progress { text-align: center; color: #8B4513; font-size: 1.2em; margin-top: 12px; font-weight: 700; }
.dark-mode .checklist-progress { color: #FFC658; }
.checklist-input-box { display: flex; gap: 8px; margin-bottom: 10px; }
.checklist-input-box input { flex: 1; padding: 10px 14px; border: 3px solid #CD853F; border-radius: 18px; font-size: 15px; outline: none; font-weight: 600; background: #FFF8DC; color: #8B4513; }
.dark-mode .checklist-input-box input { background: rgba(255,198,88,0.2); color: #FFC658; border-color: #FFC658; }
.checklist-input-box input::placeholder { color: #CD853F; opacity: 0.7; }
.checklist-input-box button { padding: 10px 20px; background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%); color: #8B4513; border: 4px solid #8B4513; border-radius: 18px; font-size: 15px; font-weight: 700; cursor: pointer; box-shadow: 0 5px 0px #8B4513; transition: all 0.3s; }
.checklist-input-box button:hover { transform: translateY(-2px); box-shadow: 0 7px 0px #8B4513; }
.checklist-list { background: #FFF8DC; border: 6px solid #CD853F; border-radius: 28px; padding: 32px; box-shadow: 0 8px 0px #CD853F; min-height: 300px; }
.dark-mode .checklist-list { background: rgba(255,198,88,0.2); border-color: #FFC658; box-shadow: 0 8px 0px #FFC658; }
.empty-message { text-align: center; color: #8B4513; font-size: 1.2em; padding: 65px 20px; font-weight: 700; }
.dark-mode .empty-message { color: #FFC658; }
.checklist-item { display: flex; align-items: center; justify-content: space-between; padding: 10px 0; border-bottom: 2px dashed #CD853F; }
.dark-mode .checklist-item { border-bottom-color: #FFC658; }
.checklist-item:last-child { border-bottom: none; }
.checklist-content { display: flex; align-items: center; gap: 16px; flex: 1; cursor: pointer; user-select: none; }
.checkbox { width: 30px; height: 30px; border: 4px solid #CD853F; border-radius: 8px; display: flex; align-items: center; justify-content: center; font-size: 16px; transition: all 0.3s; background: white; }
.dark-mode .checkbox { border-color: #FFC658; background: rgba(255,198,88,0.2); }
.checkbox.checked { background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%); border-color: #8B4513; transform: scale(1.1) rotate(5deg); }
.checklist-content span { font-size: 1.1em; color: #8B4513; transition: all 0.3s; font-weight: 600; }
.dark-mode .checklist-content span { color: #FFC658; }
.checklist-content span.completed { text-decoration: line-through; opacity: 0.5; }
.delete-btn { background: none; border: none; font-size: 26px; cursor: pointer; padding: 8px; transition: all 0.3s; opacity: 0.7; }
.delete-btn:hover { transform: scale(1.3) rotate(12deg); opacity: 1; }

/* === ÎÖ∏Ìä∏ === */
.notes-tabs { display: flex; gap: 10px; flex-wrap: wrap; margin-bottom: 22px; justify-content: center; }
.note-tab { padding: 16px 24px; background: #FFF8DC; border: 4px solid #CD853F; border-radius: 18px; color: #8B4513; font-size: 1em; cursor: pointer; font-weight: 700; box-shadow: 0 4px 0px #CD853F; transition: all 0.3s; }
.dark-mode .note-tab { background: rgba(255,198,88,0.2); color: #FFC658; border-color: #FFC658; box-shadow: 0 4px 0px #FFC658; }
.note-tab.active { background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%); color: #8B4513; border-color: #8B4513; transform: translateY(-2px); box-shadow: 0 6px 0px #8B4513; }
.note-tab:hover { transform: translateY(-2px); }
.notes-editor { background: #FFF8DC; border: 6px solid #CD853F; border-radius: 28px; padding: 32px; box-shadow: 0 8px 0px #CD853F; }
.dark-mode .notes-editor { background: rgba(255,198,88,0.2); border-color: #FFC658; box-shadow: 0 8px 0px #FFC658; }
.notes-editor textarea { width: 100%; min-height: 400px; padding: 22px; border: 4px solid #CD853F; border-radius: 18px; font-size: 15px; font-family: inherit; resize: vertical; outline: none; background: white; color: #8B4513; font-weight: 600; line-height: 1.7; }
.dark-mode .notes-editor textarea { background: rgba(255,198,88,0.15); color: #FFC658; border-color: #FFC658; }
